.nav{
    position: fixed;
    top:0;
    padding:20px;
    width:100%;
    height:30px;
    z-index:1;
    transition-timing-function: ease-in;
    transition: all 0.5s;
}
.nav__content{
    display:flex
}
.nav__logo{
    position: fixed;
    left: 0;
    top:10px;
    width:80px;
    cursor: pointer;
    object-fit: contain;
    padding-left: 20px;
}
.nav__avatar{
    position: fixed;
    right:20px;
    width:30px;
    cursor: pointer;

}
.nav__black{
background-color: black;
}